.app {
  height: 100vh;
  display: flex;
  background-color: #22223b;
  color: white;
}

.main {
  width: 75%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #020230);
  display: flex;
  flex-direction: column;
}

.top {
  height: 30%;
  position: relative;
}

.timer {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 1px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-weight: 700;
  position: absolute;
  bottom: 10px;
  left: 80px;
}
.bottom {
  height: 50%;
}
.test {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.question {
  width: 80%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), black);
  border: 0.3px solid white;
  text-align: center;
  padding: 20px;
  border-radius: 10px;
  font-size: 20px;
}
.answers {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.answer {
  width: 40%;
  padding: 10px;
  text-align: center;
  border: 1px solid white;
  border-radius: 15px;
  font-size: 20px;
  font-weight: 300;
  margin: 0 10px 20px 10px;
  cursor: pointer;
}
.answer:hover,
.answer.active {
  background-color: mediumblue;
}
.answer.correct {
  animation: correct 3s ease forwards;
}

@keyframes correct {
  0%,
  22%,
  42% {
    background: mediumblue;
  }
  20%,
  40%,
  60% {
    background: midnightblue;
  }
  62%,
  100% {
    background: green;
  }
}

.answer.wrong {
  animation: wrong 3s ease forwards;
}

@keyframes wrong {
  0%,
  22%,
  42% {
    background: mediumblue;
  }
  20%,
  40%,
  60% {
    background: midnightblue;
  }
  62%,
  100% {
    background: crimson;
  }
}
.list {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pointList {
  list-style: none;
  width: 100%;
  padding: 20px;
}
.pointListNumber {
  font-size: 18px;
  font-weight: 100;
  width: 30%;
}
.pointListItem {
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 5px;

  .pointListPoint {
    font-size: 20px;
    font-weight: 300;
  }
}
.pointListItem.active {
  background-color: teal;
}
.endText {
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.home {
  width: 250px;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  justify-content: space-around;
}
.homeInput {
  width: 100%;
  height: 40px;
  border: none;
  border-radius: 5px;
  text-align: center;
  font-size: 18px;
  
}

.homeInput:focus {
  outline: none;
}
.startButton {
  width: 100%;
  height: 40px;
  border: 1px solid white;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), black);
  color: white;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  font-weight: 500;
}
